<template>
  <div id="main-app" class="transparent">
    <div id="steps" class="row">
      <template v-if="showwhite">
        <div class="col-3">
          <button class="btn btn-primary rounded-circle" style="cursor: default;min-width: 35px">1</button>
          <span class="step-text center">{{multilanguage.configureexcursion[language]}}</span>
        </div>
        <div class="col-3">
          <button
            class="btn btn-dark rounded-circle"
            disabled="disabled"
            style="cursor: default;min-width: 35px"
          >2</button>
          <span class="step-text center">{{multilanguage.fillcheckbook[language]}}</span> 
        </div>
      </template>
      <template v-else>
        <div class="col-3">
          <button
            class="btn btn-success rounded-circle"
            style="cursor: pointer;min-width: 35px"
            @click="goback('formulario')"
          >1</button>
          <a style="cursor: pointer;" @click="goback('formulario')">
            <span class="step-text center">{{multilanguage.selectdestinationdate[language]}}</span>
          </a>
        </div>
        <div class="col-3">
          <button
            class="btn btn-success rounded-circle"
            style="cursor: pointer;min-width: 35px"
            @click="goback('dispo')"
          >2</button>
          <a style="cursor: pointer;" @click="goback('dispo')">
            <span class="step-text center">{{multilanguage.selectexcursion[language]}}</span>
          </a>
        </div>
        <div class="col-3">
          <button class="btn btn-primary rounded-circle" style="cursor: default;min-width: 35px">3</button>
          <span class="step-text center">{{multilanguage.configureexcursion[language]}}</span>
        </div>
        <div class="col-3">
          <button
            class="btn btn-dark rounded-circle"
            disabled="disabled"
            style="cursor: default;min-width: 35px"
          >4</button>
          <span class="step-text center">{{multilanguage.fillcheckbook[language]}}</span>
        </div>
      </template>
    </div>
    <div class="row">
      <div class="col-12 mt-5">
        <div class="excursiontitle">{{title}}</div>
      </div>
    </div>
    <form>
      <div class="row">
        <div class="col-sm-12 col-md-12 col-lg-6">
          <Ficha :rate="this.$route.query.key"></Ficha>
        </div>
        <div class="col-sm-12 col-md-12 col-lg-6">
        <div class="calendar-title text-center">{{multilanguage.changedates[language]}}</div>
        <div v-if="loaderdate " class="text-center mt-5 pt-5">
          <div class="spinner-border text-primary"></div>
        </div>
        <div v-else>
          <div v-if="charged" class="my-calendar-wrapper">
              <v-date-picker
                width="100%"
                mode="single"
                is-double-paned
                v-model="selectedValue"
                
                :is-linked="true"
                :is-expanded="true"
                :available-dates="availabledates"
                :disabled-dates="disableddates"
                :is-inline="true"
                :disabled-attribute="disableattr"
                :drag-attribute="dragattr"
                :select-attribute="selectattr"
                :theme-styles="themeStyles"
                tint-color="#FFE259"
                show-caps
              ></v-date-picker>
               <div class="row calendar-legend">
              <div class="col ">
                <div id="availablecolor" class="box available" :style="'background:'+ availablecolor">
                  
                </div>
                <div class=" text-legend" >{{ multilanguage.available[language] }}</div>
              </div>
              <div class="col ">
                <div class="box noavailable" :style="'background:'+ noavailablecolor">
                </div>
                <div class="text-legend" >{{ multilanguage.noavailable[language] }}</div>
              </div>
              <div class="col-sm ">
                <div class="box selected" :style="'background:'+ selectedcolor">
                </div>
                <div class="text-legend" >{{ multilanguage.selected[language] }}</div>
              </div>
            </div>
             <div class="form-group">
                <input
                  data-error="wrong"
                  id="checkindates"
                  class="validate form-control" 
                  style="display:none"/>
                <div class="invalid-feedback">
              <span id="checkindatesaux-msg"> </span>
            </div>
              </div>
            </div>
            <div v-else>
              <template v-if="showwhite">
                <a
                  @click="goback('formulario') "
                  style="cursor: pointer"
                >{{multilanguage.nocalendar[language]}}</a>
              </template>
              <template v-else>
                <a
                  @click="goback('formulario')"
                  style="cursor: pointer"
                >{{multilanguage.nocalendar[language]}}</a>
              </template>
            </div>
          </div>
        </div>
        </div>
      <div class="row">
        <template v-if="detalles">
          <div class="col-sm-12 col-md-12 col-lg-6 mt-5">
            <div
              class="calendar-title text-center"
            >{{multilanguage.configureyourexcursion[language]}}</div>

            <div class="row">
              <div
                class="col-12  form-group"
                v-if="detalles.variants.length > 0"
              >
               <div @click="hidevariant=false">
                  <label for="variante" class="active">{{multilanguage.variant[language]}}</label>
                  <input
                    class="form-control"
                    readonly
                    id="variante"
                    v-model="variante.name"
                    @click ="listenEsc()"
                    style="background-color:white;"
                  />
                </div>
                <div class="list-group select-container" :class="{invisible: hidevariant}" >
                  <div
                    @click="variante = d"
                    v-bind:class="{active :variante === d}"
                    v-for="(d,indexv) in detalles.variants"
                    v-bind:value="d"
                    class="btn list-group-item list-group-item-action"
                    style="cursor: pointer"
                    :key="'k'+indexv"
                  >
                    <div class="row">
                      <div class="col text-left align-middle resalted">{{ d.name }}</div>
                      <template v-if="d.bestDeal.offer !== true">
                        <div
                          class="col text-right align-middle resalted"
                        >{{ d.bestDeal.retailPrice.value }} {{ d.bestDeal.retailPrice.value.currencyIsoCode }}</div>
                      </template>
                      <template v-else>
                        <div class="col text-right resalted">
                          <i
                            class="tiny material-icons mini-icon align-middle"
                            data-toggle="tooltip"
                            data-placement="bottom"
                            :title="d.bestDeal.offerText"
                            style="cursor: pointer;padding-right: 5px;"
                          >new_releases</i>
                          <span
                            class="align-middle"
                          >{{ d.bestDeal.retailPrice.value }} {{ d.bestDeal.retailPrice.value.currencyIsoCode }}</span>
                          <!--<i v-if="ocupacion.seleccionado === price.key" class="small material-icons" >check_box</i>-->
                        </div>
                      </template>
                    </div>
                    <div class="row">
                      <div class="col">{{ d.description }}</div>
                    </div>
                  </div>
                </div>
               <!-- <label for="variante">{{multilanguage.variant[language]}}</label>
                <select
                  id="variante"
                  class="form-control"
                  v-model="variante"
                  style="cursor: pointer"
                >
                  <option v-for="(d,indexv) in variantes" v-bind:value="d" :key="indexv">
                    <div>
                      <b>{{ d.name }}</b>
                      <template v-if="d.bestDeal.offer !== true">
                        <span class="secondary-content black-text">
                          <b>{{ d.bestDeal.retailPrice.value}}</b>
                          {{d.bestDeal.retailPrice.currencyIsoCode}}/ {{d.pricePer}}
                        </span>
                      </template>
                      <template v-else>
                        <span class="badge red white-text">
                          <b>{{ d.bestDeal.retailPrice.value}}</b>
                          {{d.bestDeal.retailPrice.currencyIsoCode}}/ {{d.pricePer}}
                        </span>
                      </template>
                    </div>
                    <div>{{d.description}}</div>
                  </option>
                </select>-->
                <div class="invalid-feedback">
              <span id="varianteaux-msg"> </span>
            </div>
              </div>
             
            </div>
            <div class="row">
               <div class="col-sm-12 form-group" v-if="detalles.shifts">
                <label for="turno">{{multilanguage.shift[language]}}</label>
                <select id="turno" v-model="turno" class="form-control" style="cursor: pointer">
                  <option v-for="(d,indext) in turnos" v-bind:value="d" :key="indext">{{ d.name }} ({{ d.time }})</option>
                </select>
                <div class="invalid-feedback">
              <span id="turnoaux-msg"> </span>
            </div>
              </div>
              <div class="col-sm-12 form-group">
                <label
                  for="hotel"
                  data-error="wrong"
                  data-success="right"
                >{{multilanguage.pickuppoint[language]}}</label>

                <select id="hotel" v-model="hotelk" class="form-control" style="cursor: pointer">
                  <option v-for="(d,index) in pickups" v-bind:value="d" :key="index">{{d.name}}</option>
                </select>
                <div class="invalid-feedback">
              <span id="hotelaux-msg"> </span>
            </div>
              </div>

            
            </div>

            <div class="row">
                <div class="col-sm-12 col-md-6 col-lg-4 form-group">
                <label for="idioma">{{multilanguage.language[language]}}</label>
                <select
                  id="idioma"
                  v-model="idiomaguiak"
                  class="form-control"
                  style="cursor: pointer"
                >
                  <option v-for="(d,index) in languages" v-bind:value="d" :key="index">{{d.name}}</option>
                </select>
                  <div class="invalid-feedback">
              <span id="idiomaaux-msg"> </span>
            </div>
              </div>
              <div class="col-sm-12 col-md-6 col-lg-4 form-group">
                <label for="adultos">{{multilanguage.adult[language]}}</label>
                <input
                  type="number"
                  data-error="wrong"
                  id="adultos"
                  v-model="key.adultos"
                  min="1"
                  max="99"
                  class="validate form-control"
                  required
                />
                <div class="invalid-feedback">
              <span id="adultosaux-msg"> </span>
            </div>
              </div>

              <div class="col-sm-12 col-md-6 col-lg-4 form-group">
                <label
                  for="ninos"
                  class="active"
                  data-error="wrong"
                >{{multilanguage.children[language]}}</label>
                <input
                  type="number"
                  id="ninos"
                  v-model="key.ninos"
                  min="0"
                  max="99"
                  class="validate form-control"
                  aria-invalid="true"
                />
                <span class="helper-text"></span>
              </div>
            </div>
          </div>

          <div class="col-sm-12 col-md-12 col-lg-6 mt-5">
            <div class="calendar-title text-center">{{multilanguage.prices[language]}}</div>
            <div class="row" v-if="total">
              <div class="col-12">
                <table class="table mt-4 mb-4">
                  <thead>
                    <tr>
                      <th>{{multilanguage.description[language]}}</th>
                      <th class="text-right">{{multilanguage.value[language]}}</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-if=" total.value" style="margin-top:30px;">
                      <td class="pt-3">{{multilanguage.total[language]}}</td>
                      <td class="text-right pt-3">
                        {{ total.value.value }} {{
                        total.value.currencyIsoCode}}
                      </td>
                    </tr>
                  </tbody>
                </table>
                <hr class="pt-3" />
              </div>
              <div class="col-12 text-center button-container" v-if="total.available">
                <!-- <i class="material-icons green small circle">done</i><br/>{{multilanguage.available[language]}}-->
                <button
                  type="button"
                  class="btn btn-primary"
                  v-on:click="book"
                >{{multilanguage.goon[language]}}</button>
              </div>
              <div class="col-12 text-center button-container" v-else>
                <button
                  type="button"
                  class="btn btn-primary"
                  disabled
                >{{multilanguage.noavailable[language]}}</button>
              </div>
            </div>
          </div>
        </template>
      </div>
    </form>
  </div>
</template>

<script>
import axios from "axios";
import helper from "@/script/helper";
import Ficha from "@/components/commons/Ficha";

const langfile = require("../../assets/lang/content.json");
var iswhitebrand = false;

if (
  globaldata.whitebrandexcursion !== null &&
  globaldata.whitebrandexcursion !== ""
) {
  iswhitebrand = true;
}
export default {
  name: "Rates",
  data: function() {
    return {
      hidevariant: true,
      availablecolor:globaldata.availablecolor?globaldata.availablecolor:"#4caf50",
      noavailablecolor:globaldata.noavailablecolor?globaldata.noavailablecolor:"#F84744",
      selectedcolor:globaldata.selectedcolor?globaldata.selectedcolor:"#ffc107",
      showwhite: iswhitebrand,
      title: this.$route.query.excursionname,
      disponibilidad: null,
      availabledates: [],
      disableddates: null,
      detalles: null,
      selectedValue: this.createDate(
        this.$route.query.entrada.replace(/-/g, "")
      ),
      pickups: null,
      languages: null,
      turno: null,
      variante: null,
      variantes: [],
      turnos: [],
      charged: null,
      titular: null,
      comentarios: null,
      idiomaguiak: null,
      hotelk: null,
      key: {
        key: null,
        ratekey: null,
        datekey: null,
        variantesel: null,
        turnosel: null,
        adultos: 1,
        ninos: 0,
        idiomaguia: null,
        pickup: null
      },
      avdates: [],
      total: null,
      loaderdate: null,
      language: globaldata.language?globaldata.language:"es",
      multilanguage: langfile.multilanguage,
      from_page: null,
      to_page: null
    };
  },
  components: {
    Ficha: Ficha
  },
  computed:{
    themeStyles () {
      return {
        wrapper: {
          background: "#fff",
          borderBotom:"solid rgba(0, 0, 0, 0.2) 1px",
          /*border: "solid rgba(0, 0, 0, 0.2) 1px",
          borderRadius: "5px",
          padding: "10px"*/
        },
        headerTitle: {
          padding: "10px 0px",
          fontSize: "25px"
        },
        dayCellNotInMonth: {
          opacity: 0.7
        },
        dayCell: {
          background: this.availablecolor,//"#4caf50",
          color: globaldata.availabletext?globaldata.availabletext:"white",//"#fff",
          padding: "0px 0px",
          margin: "0px"
        },
        dayContent: {
          padding: "0px",
          margin: "0px",
          fontSize: "16px"
        }
      }
      }
      ,
      disableattr() {
        return{
        key: "disabled",
        highlight: {
          backgroundColor: this.noavailablecolor,//"#F84744",
          opacity: 1,
          animated: false
        },
        contentStyle: {
         color: globaldata.noavailabletext?globaldata.noavailabletext:"white"
        }
      }
      },
      enableattr() {
      return{
          key: "available",
          highlight: {
            backgroundColor: this.availablecolor,// "#4caf50",
            opacity: 1,
            animated: false
          },
          contentStyle: {
            color: globaldata.noavailabletext?globaldata.noavailabletext:"white"
          }
        }
            
      },
      dragattr ()  {
        return{
        key: "drag",
        highlight: {
          backgroundColor: this.selectedcolor,// "#ffc107",
          opacity: 1,
          animated: false
        },
        contentStyle: {
         color: globaldata.selectedtext?globaldata.selectedtext:"black",
        }
        }
      },
      selectattr () {
        return {
        key: "select",
        highlight: {
          backgroundColor: this.selectedcolor, //"#ffb300",
          opacity: 1,
          animated: false
        },
        contentStyle: {
          color: globaldata.selectedtext?globaldata.selectedtext:"black"
        }
        }
      }
  },
  watch: {
    "key.ninos": function(val) {
      this.valorar();
    },
    "key.adultos": function(val) {
      this.valorar();
    },
    hotelk: function(val) {
      this.$data.key.pickup = this.$data.hotelk.id;
      this.valorar();
    },
    idiomaguiak: function(val) {
      this.$data.key.idiomaguia = this.$data.idiomaguiak.id;
      this.valorar();
    },
    variante: function(val) {
      this.$data.key.variantesel = this.$data.variante.key;
      this.valorar();
    },
    turno: function(val) {
      if (this.$data.turno && val) {
        this.$data.key.turnosel = this.$data.turno.id;
        this.cambioturno(val);
        this.valorar();
      }
    },
    selectedValue: function(val) {
      this.$data.key.datekey = this.formatDate(val).replace(/-/g, "");
      this.getrates(this.formatDate(val).replace(/-/g, ""));
    }
  },
  methods: {
      listenEsc: function(e){
      document.addEventListener("keydown", this.closeselect, false);
      document.addEventListener("click",this.closeselect, true);
    },
    closeselect: function(e) {
      var keyCode = e.keyCode;
        if(keyCode === 27 || (e.type ==="click" && this.$data.hidevariant ===false)){
         this.$data.hidevariant = true
         this.removeListener()
        }
    },
    removeListener: function(){
      document.removeEventListener("keydown", this.closeselect)
    },
    valorar: function() {
      var d = this.$data;
      if (d.key) {
        if (
          d.selectedValue &&
          d.key.variantesel &&
          d.key.variantesel !== "" &&
          d.key.turnosel &&
          d.key.turnosel !== "" &&
          d.key.idiomaguia &&
          d.key.idiomaguia !== "" &&
          d.key.pickup &&
          d.key.pickup !== ""
        ) {
          axios({
            method: "get",
            url:
              globaldata.baseurl + "/activity/check/" + this.$route.query.key,
            params: {
              language: d.language,
              adults: d.key.adultos,
              children: d.key.ninos,
              date: d.key.datekey,
              variant: d.key.variantesel,
              shift: d.key.turnosel,
              pickup: d.key.pickup,
              activityLanguage: d.key.idiomaguia
            }
          })
            .then(function(response) {
              d.total = response.data;
              d.key.ratekey = d.total.key;
            })
            .catch(function(error) {
              console.log(error);
            });
        } else {
          d.total = null;
          d.key.ratekey = null;
        }
      }
    },
    goback(page) {
      switch (page) {
        case "formulario":
          this.$router.push({
            name: "main",
            query: {
              entrada: this.$route.query.entrada,
              destinoname: this.$route.query.destinoname,
              destino: this.$route.query.destino
            }
          });
          break;
        case "dispo":
          this.$router.push({
            name: "excursion_dispo",
            query: {
              key: this.$route.query.key,
              entrada: this.$route.query.entrada,
              salida: this.$route.query.salida,
              destinoname: this.$route.query.destinoname,
              destino: this.$route.query.destino,
              excursionname: this.$route.query.name,
              idioma: this.$data.language
            }
          });
          break;
        default:
      }
    },
    cambioturno: function(val) {
      this.$data.pickups = null;
      this.$data.languages = null;
      this.$data.idiomaguiak = null;
      this.$data.hotelk = null;

      if (val.pickups) {
        this.$data.pickups = val.pickups;
        this.$data.hotelk = val.pickups[0];
        console.log("this.$data.hotelk ");
        console.log(this.$data.hotelk);
      }

      if (val.languages) {
        this.$data.languages = val.languages;
        this.$data.idiomaguiak = val.languages[0];
      }
    },
    book: function(e) {
      e.preventDefault();
      if (this.checkForm()) {
        this.$router.push({
          name: "excursion_detalle",
          query: {
            ratekey: this.$data.key.ratekey,
            datekey: this.$data.key.datekey.replace(/-/g, ""),
            variantesel: this.$data.variante.name,
            turnosel: this.$data.turno.name,
            adultos: this.$data.key.adultos,
            ninos: this.$data.key.ninos,
            idiomaguia: this.$data.idiomaguiak.name,
            hotel: this.$data.hotelk.name,
            destinoname: this.$route.query.destinoname,
            destino: this.$route.query.destino,
            excursionname: this.$route.query.excursionname,
            key: this.$route.query.key,
            entrada:
              this.$data.key.datekey.toString().substr(0, 4) +
              "-" +
              this.$data.key.datekey.toString().substr(4, 2) +
              "-" +
              this.$data.key.datekey.toString().substr(6, 2),
            idioma: this.$data.language
          }
        });
      }
    },
    checkForm: function(e) {
      var existsErrors = false
      this.cleanErrors()

      if (this.key.datekey === null || this.key.datekey === "") {
         this.setValidationError('checkindates', 'checkindatesaux-msg', this.multilanguage.errordateempty[this.language])
         existsErrors = true
      }

      if (this.key.pickup === null || this.key.pickup === "") {
        this.setValidationError('hotel', 'hotelaux-msg', this.multilanguage.errorpickupempty[this.language])
        existsErrors = true
        
      }
      if (this.key.idiomaguia === null || this.key.idiomaguia === "") {
        this.setValidationError('idioma', 'idiomaaux-msg', this.multilanguage.errorlanguageempty[this.language])
        existsErrors = true
      }

      if (this.key.variantesel === null || this.key.variantesel === "") {
        this.setValidationError('variante', 'varianteaux-msg', this.multilanguage.errorvariantempty[this.language])
        existsErrors = true
      }

      if (this.key.turnosel === null || this.key.turnosel === "") {
        this.setValidationError('turno', 'turnoaux-msg', this.multilanguage.errorshiftempty[this.language])
        existsErrors = true
      }

      if (this.key.adultos < 1) {
        this.setValidationError('adultos', 'adultosaux-msg', this.multilanguage.errornopeople[this.language])
        existsErrors = true
      }
       
      if (existsErrors) {
        return false;
      }
      return true;
    },
    setValidationError (field, msgField, msg) {
      var d = this.$data

      var element = document.getElementById(field)
      element.classList.add('is-invalid')
      element.classList.add('form-control')
      element.focus()

      if (msgField !== null && msgField !== ' ') {
        var elementmsg = document.getElementById(msgField)

        elementmsg.innerHTML = msg
      }
    },
     cleanErrors: function () {
      var d = this.$data

      var element = document.getElementById('variante')
      element.setAttribute('class', 'form-control bg-white')

      var element = document.getElementById('hotel')
      element.setAttribute('class', 'form-control bg-white')

      var element = document.getElementById('turno')
      element.setAttribute('class', 'form-control bg-white')

      var element = document.getElementById('idioma')
      element.setAttribute('class', 'form-control bg-white')

      var element = document.getElementById('adultos')
      element.setAttribute('class', 'form-control bg-white')

      element = document.getElementById('checkindates')
      element.setAttribute('class', 'form-control bg-white')
    },
    createDate: function(str) {
      var stringdate = str.toString();

      var year = stringdate.substr(0, 4);
      var month = stringdate.substr(4, 2);
      var day = stringdate.substr(6, 2);
      var formatdate = new Date(year, month - 1, day);

      return formatdate;
    },
    formatDate: function(date) {
      var d = new Date(date);
      var month = "" + (d.getMonth() + 1);
      var day = "" + d.getDate();
      var year = d.getFullYear();

      if (month.length < 2) month = "0" + month;
      if (day.length < 2) day = "0" + day;

      return [year, month, day].join("-");
    },
    getrates: function(entrada) {
      var d = this.$data;
      var _this = this;
      axios({
        method: "get",
        url: globaldata.baseurl + "/activity/rates/" + this.$route.query.key,
        params: {
          date: entrada,
          language: d.language
        }
      })
        .then(function(response) {
          d.detalles = response.data;

          d.charged = true;
          d.variantes = d.detalles.variants;
          //addedcoder
            d.variantes.push({
              key: "gen-175-176",
              name: "General 3",
              description:
                "una  alasdasd asdkahsdjuhasd asodhasd asudhas dasodhaoisdn asoidhjasdl aosidhjalk sdaoisdjaloksd aosidjalksd aliksdjalksd alisdjalksd aloiksjal sdaloisdjalsd asdoiashdjlaksd asjdhaoisdhnjoaisnhdka saoisdhoaisdoasbd",
              bestDeal: {
                retailPrice: { currencyIsoCode: "EUR", value: 412.03 },
                netPrice: null,
                commission: null,
                offerText: null,
                beforeOfferPrice: null,
                offer: false
              }
            });
            d.variantes.push({
              key: "gen-175-177",
              name: "General 4",
              description:
                "una  alasdasd asdkahsdjuhasd asodhasd asudhas dasodhaoisdn asoidhjasdl aosidhjalk sdaoisdjaloksd aosidjalksd aliksdjalksd alisdjalksd aloiksjal sdaloisdjalsd asdoiashdjlaksd asjdhaoisdhnjoaisnhdka saoisdhoaisdoasbd",
              bestDeal: {
